.main {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;

  --image-border-radius: 20px;
  --animation-image-duration: 6s;
}

.grid {
  position: absolute;
  left: 0;
  top: 0;
}

.canvas {
  /* border: 1px solid rgb(155, 155, 155); */
  /* border-radius: var(--image-border-radius); */
  /* box-shadow: inset 0px 0px 10px 0px #0f5ecd; */
  /* animation: 3s infinite alternate ease-out breathing-color; */
  display: inline-block;
  grid-area: c;
  left: 0;
  top: 0;

  z-index: 3;
}
.croppedCanvas {
  animation: 1.5s infinite alternate ease-out blink;
}

@keyframes blink {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.3;
  }
}

canvas {
  position: absolute;
}

@keyframes breathing-color {
  from {
    box-shadow: 0px 0px 10px 0px #0f5ecd;
  }
  to {
    box-shadow: 0px 0px 20px 0px #04306e;
  }
}

.tools {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 800px;
  height: 50px;

  grid-area: a;
}

.brushSizes {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.brushSize {
  background-color: white;
  border-radius: 100px;
  border: 4px solid black;
  transition: all ease 0.3s;
}
.brushSize + .brushSize {
  margin-left: 10px;
}
.brushSize--selected {
  border: 4px solid rgb(134, 134, 134);
  margin: 0px;
}

.colorPalette {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.color {
  height: 30px;
  width: 30px;
  border-radius: 30px;
  box-sizing: content-box;
  border: 4px solid black;
  transition: all ease 0.3s;
}
.color + .color {
  margin-left: 10px;
}
.color--selected {
  border: 4px solid rgb(207, 207, 207);
  margin: 0px;
}

.button {
  box-shadow: 0px 0px 7px #1677ff !important;
  height: 50px;
  width: 200px;
  font-size: 20px;
  font-weight: bold;
  grid-area: e;
  margin-top: 45px;
  justify-self: center;
}

.intro {
  width: 350px;

  font-size: 24px;
  line-height: 1.1666666667;
  font-weight: 600;
  letter-spacing: 0.009em;
  font-family: SF Pro Display, SF Pro Icons, Helvetica Neue, Helvetica, Arial,
    sans-serif;
  color: #cecece;

  position: relative;
  z-index: 1;

  grid-area: d;
  align-self: start;
}

.intro h1 {
  display: inline-block;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(180deg, #ffe993, #eb831c);
  overflow: hidden;
  background-color: #ffe993 0%;

  animation-name: fadeInLeft;
  animation-duration: 1s;
  animation-fill-mode: both;
}

.intro div {
  animation-name: fadeInLeft;
  animation-duration: 1s;
  animation-fill-mode: both;
}

.image {
  align-self: center;
  grid-area: c;
  width: 800px;
  height: 600px;
  object-fit: cover;

  animation-name: fadeInStartsWithRoundCorner;
  animation-fill-mode: forwards;

  position: relative;
  z-index: 2;
}

@keyframes fadeInStartsWithRoundCorner {
  from {
    opacity: 0;
    border-radius: var(--image-border-radius);
  }

  to {
    opacity: 1;
    border-radius: 0;
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-50px, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.dummy {
  grid-area: b;

  width: 0;
  animation-name: growWidth;
  animation-fill-mode: forwards;
  animation-duration: 800ms;
}
@keyframes growWidth {
  from {
    width: 0;
  }

  to {
    width: 350px;
  }
}

.fadeOut {
  animation-name: fadeOut;
  animation-fill-mode: forwards;
}
.animationImage {
  animation-duration: var(--animation-image-duration);
}

.fadeOutFast {
  animation-name: fadeOut;
  animation-duration: 600ms;
  animation-fill-mode: forwards;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.processedImage {
  object-fit: cover;
  position: absolute;

  animation-name: fadeIn;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  z-index: 4;

  cursor: pointer;
  mix-blend-mode: lighten;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.modal {
  position: absolute;
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: 50px min-content 95px;
  grid-template-areas:
    "a b"
    "c d"
    "e f";
  row-gap: 20px;
  column-gap: 80px;

  z-index: 20;
}

.modalBackground {
  position:absolute;
  left: 0;
  top:0;
  width: 100vw;
  height: 100vh;

  background-color: rgba(0,0,0,0.75);
  z-index: 19;
}